@use '_mixin' as m;

.header {
  // .header__container

  &__container {
    min-height: m.toRem(76);
    display: grid;
    grid-template-columns: m.toRem(150) 1fr m.toRem(150);
    align-items: center;
    gap: m.toRem(15);
    @media (max-width: m.toEm(992)) {
      grid-template-columns: auto 1fr;
    }
    @media (max-width: m.toEm(768)) {
      grid-template-columns: auto 1fr;
    }
  }

  // .header__logo

  &__logo {
  }

  // .header__search

  &__search {
    position: relative;
    justify-self: center;
    width: 100%;
    max-width: m.toRem(480);
  }

  // .header__search-btn

  &__search-btn {
    position: absolute;
    left: m.toRem(16);
    top: 50%;
    transform: translate(0px, -50%);
    @media (max-width: m.toEm(768)){
          left: m.toRem(12);
    }
  }

  // .header__search-icon

  &__search-icon {
    width: m.toRem(14);
    height: m.toRem(14);
  }

  // .header__search-input

  &__search-input {
    padding: m.toRem(12) m.toRem(12) m.toRem(14) m.toRem(42);
    display: flex;
    align-items: center;
    font-size: m.toRem(14);

    height: m.toRem(44);
    border-radius: 7px;
    font-size: m.toRem(14);
    box-shadow: 0 0 0 1px var(--grey);
    color: #bababa;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: box-shadow 0.3s ease;
    &:focus {
      outline: none;
      box-shadow: 0 0 0 1px var(--main-color);
    }

    @media (max-width: m.toEm(768)){
          padding: m.toRem(12) m.toRem(12) m.toRem(14) m.toRem(35);
    }
  }
}
.logo {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  gap: m.toRem(10);
  @media (max-width: m.toEm(768)) {
    gap: m.toRem(5);
  }
  // .logo__icon

  &__icon {
    flex-shrink: 0;
    width: m.toRem(30);
    height: m.toRem(30);
    @media (max-width: m.toEm(768)) {
      width: m.toRem(25);
      height: m.toRem(25);
    }
  }

  // .logo__text

  &__text {
    font-weight: 700;
    font-size: m.toRem(24);
    line-height: 110%;
    @media (max-width: m.toEm(768)) {
      font-size: m.toRem(20);
    }
  }
}
