@use '_mixin' as m;

.pools {
  padding: m.toRem(30) m.toRem(0);

  // .pools__body

  &__body {
    padding: 0 m.toRem(15);
    max-width: m.toRem(1494);
    margin: 0 auto;
  }

  // .pools__title

  &__title {
    margin-bottom: m.toRem(10);
    font-weight: 700;
    font-size: m.toRem(48);
    line-height: 110%;
    @media (max-width: m.toEm(768)) {
      font-size: m.toRem(38);
    }
  }

  // .pools__filters

  &__filters {
    margin-bottom: m.toRem(20);
  }
}

.pool {
  display: flex;
  align-items: center;
  gap: m.toRem(12);
  overflow: hidden;

  // .pool__items

  &__items {
    display: flex;
    align-items: center;
    flex-shrink: 0;
  }

  // .pool__logo

  &__logo {
    flex-shrink: 0;
    width: m.toRem(32);
    height: m.toRem(32);
    border-radius: 50%;
    overflow: hidden;

    &--last {
      margin-left: m.toRem(-10);
    }
  }

  // .pool__info

  &__info {
    overflow: hidden;
    text-overflow: ellipsis;
    display: flex;
    flex-direction: column;
    align-items: start;
    gap: m.toRem(5);
  }

  // .pool__name

  &__name {
    font-weight: 700;
    text-transform: uppercase;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  // .pool__fee

  &__fee {
    padding: m.toRem(3) m.toRem(6);
    font-weight: 700;
    font-size: m.toRem(10);
    line-height: 100%;
    text-align: center;
    color: var(--white);
    background-color: var(--main-color);
    border-radius: 100px;
  }
}

.table {
  @media (max-width: m.toEm(992)) {
    overflow: auto;
    margin-left: m.toRem(-15);
    margin-right: m.toRem(-15);

    padding-left: m.toRem(15);
    padding-right: m.toRem(15);

    &::-webkit-scrollbar {
      display: none;
    }
  }

  &__body {
    background: var(--white);
    border-radius: 6px;
    border: 1px solid var(--grey);

    @media (max-width: m.toEm(992)) {
      width: m.toRem(900);
    }
  }

  &__row {
    &:not(:last-child) {
      border-bottom: 1px solid var(--grey);
    }

    &:first-child {
      border-bottom: none;
    }
  }

  &__items {
    padding: 0 m.toRem(20);
    height: m.toRem(62);
    display: grid;
    grid-template-columns:
      minmax(m.toRem(25), m.toRem(30)) minmax(m.toRem(150), m.toRem(395)) minmax(
        m.toRem(80),
        m.toRem(246)
      )
      minmax(m.toRem(80), m.toRem(255)) minmax(m.toRem(50), m.toRem(232)) minmax(
        m.toRem(50),
        m.toRem(232)
      )
      minmax(m.toRem(85), m.toRem(85));
    align-items: center;
    font-size: m.toRem(14);
    gap: m.toRem(10);
    color: #0c0c0c;

    @media (max-width: m.toEm(1400)) {
      font-size: m.toRem(12);
    }

    @media (max-width: m.toEm(768)) {
      padding: 0 m.toRem(10);
      height: m.toRem(50);
    }

    &--title {
      height: m.toRem(50);
      font-size: m.toRem(13);
      font-weight: 500;
      color: var(--main-color);
      background: var(--grey);
    }
  }

  &__item {
    display: flex;
    align-items: center;
    height: 100%;

    span {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  &__item-star {
    width: m.toRem(18);
    height: m.toRem(18);
  }

  // .table__item-btn

  &__item-btn {
    padding: m.toRem(7) m.toRem(16);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--main-color);
    transition: background 0.3s ease 0s;
    color: var(--white);
    font-weight: 500;
    font-size: m.toRem(14);
    line-height: 130%;
    border-radius: 6px;

    @media (any-hover: hover) {
      &:hover {
        background: #000000;
      }
    }
  }
}
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: m.toRem(20);

  // .filters__options

  &__options {
    display: flex;
    align-items: center;

    gap: m.toRem(20);
    @media (max-width: m.toEm(768)) {
      gap: m.toRem(12);
    }
  }

  // .filters__star

  &__star {
    flex-shrink: 0;
    width: m.toRem(22);
    height: m.toRem(22);
    @media (max-width: m.toEm(768)) {
      width: m.toRem(20);
      height: m.toRem(20);
    }
  }

  // .filters__item
  &__item {
    position: relative;
    width: m.toRem(160);
    flex: 0 1 m.toRem(160);

    &.active {
      .filters__arrow {
        transform: rotate(-90deg);
      }
    }
  }

  // .filters__btn

  &__btn {
    padding: m.toRem(5) m.toRem(14) m.toRem(5) m.toRem(20);
    min-height: m.toRem(44);
    width: 100%;
    display: flex;
    align-items: center;
    gap: m.toRem(7);
    justify-content: space-between;
    background: var(--white);
    border: 1px solid var(--grey);
    border-radius: 10px;
    @media (max-width: m.toEm(768)) {
      padding: m.toRem(5) m.toRem(10) m.toRem(5) m.toRem(10);
      min-height: m.toRem(40);
    }
  }

  // .filters__title

  &__title {
    transition: color 0.3s ease 0s;
    pointer-events: none;
    font-size: m.toRem(14);
    color: #bababa;

    @media (max-width: m.toEm(768)) {
      font-size: m.toRem(12);
    }
  }

  // .filters__icon

  &__icon {
    flex-shrink: 0;
    pointer-events: none;
    width: m.toRem(16);
    height: m.toRem(16);
    transition: transform 0.3s ease 0s;

    @media (max-width: m.toEm(768)) {
      width: m.toRem(14);
      height: m.toRem(14);
    }

    .active & {
      transform: rotate(-180deg);
    }
  }
}

.dropdown-item {
  &.active {
    .dropdown-filter {
      opacity: 1;
      visibility: visible;
      transform: translate(0, 0);
    }
  }
}

.dropdown-filter {
  padding: m.toRem(10) 0;
  position: absolute;
  width: 100%;
  z-index: 100;
  display: flex;
  flex-direction: column;
  gap: m.toRem(2);
  top: calc(100% + m.toRem(5));
  left: 0;
  background: var(--white);
  border: 1px solid var(--grey);
  border-radius: 10px;
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transform: translate(0, m.toRem(-10));
  transition: opacity 0.3s ease;
  &__item {
    padding: m.toRem(10) m.toRem(20);
    cursor: pointer;
    transition: background 0.3s ease 0s;
    @media (any-hover: hover) {
      &:hover {
        background-color: var(--grey);
      }
    }
  }
}
