@use '_mixin' as m;

body {
  background: #fff;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;

  @supports (overflow: clip) {
    overflow: clip;
  }

  // Прижимаем footer
  > main {
    flex: 1 1 auto;
  }
}
[class*='__container'] {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
  width: 100%;
}
